.track {
  padding-top: 20px;
  min-height: 500px;

  .overlay_text {
    position: absolute;
    text-align: left;
    top: 50%;
    left: 0;
    background-color: #ffffff5c;
    padding: 10px 40px;
    h6 {
      font-size: 1.5rem;
      color: $blue2;
    }
    p {
      color: $blue2;
      font-size: 2rem;
      font-weight: bold;
      margin-bottom: 0;
    }
  }
}

@media (max-width: 991px) {
  .track {
    padding-top: 0;

    .overlay_text {
      top: 40%;
      padding: 10px 20px;
      h6 {
        font-size: 1rem;
      }
      p {
        font-size: 1.2rem;
      }
    }
  }
}
