.newsletter {
  background: $orange1;
  padding: 2rem 0;
  font-weight: bold;
  p {
    font-size: 2rem;
    margin-bottom: 0;
    color: $blue2;
  }
  .email-group {
    position: relative;
    input {
      width: 100%;
      height: 48px;
      padding: 5px 25px;
      border: 0;
      transition: all 0.3s ease-in-out;
      &:focus {
        outline: 0;
        background: #eee;
        transition: all 0.3s ease-in-out;
      }
    }
    .btn-primary {
      right: 25px;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}

@media (max-width: 991px) {
  .newsletter p {
    font-size: 1.2rem;
    text-align: center;
    padding-bottom: 20px;
  }
  .newsletter .email-group .btn-primary {
    font-size: 0.7rem;
    right: 10px;
  }
}
