.account-dashbaord {
  padding-bottom: 20em;
  .aside-nav {
    .aside__list {
      border: 1px solid $orange1;
      padding: 5px;
      .aside__item {
        &:not(:last-of-type) {
          border-bottom: 1px solid $gray3;
        }
        padding: 10px;
        a {
          transition: all 0.3s ease-in-out;
          color: $gray3;
          &:hover {
            color: $orange1;
            transition: all 0.3s ease-in-out;
            padding-inline-start: 3px;
          }
        }
        &.active a {
          color: $orange1;
          font-weight: bold;
        }
      }
    }

    .btn-signout {
      color: $orange1;
      &:focus {
        box-shadow: none;
      }
    }
  }
}
