.form {
  margin-top: 10px;

  p {
    font-size: 1rem;
    font-weight: 700;
  }
  label {
    font-size: 0.8rem;
    font-weight: 600;
  }

  .form-group {
    .form-control {
      border-radius: 100px;
      &:focus {
        box-shadow: 0 0 0 0.2rem $orange1;
      }
    }
  }
  .f-btns-c {
    text-align: center;
  }

  button {
    margin: 0 auto;
    display: block;
    margin-bottom: 10px;
    width: 80%;
    height: 38px;
  }
  .textarea {
    border-radius: 10px !important;
  }
}

.form--login {
  label {
    color: $gray1;
  }
}

.form--register {
  background-color: $gray3;
  padding: 1.5em 1em;
  label {
    font-size: 0.8rem;
    font-weight: 700;
  }
}

.form-b-t-o {
  border-top: 5px solid $orange1;
}
.form-b-c-i-g {
  .form-control {
    background-color: $gray3;
    border: 1px solid $gray1;
  }
}












@media (max-width: 991px) {
  .form {
    p {
      font-size: 0.9rem;
      font-weight: 700;
    }
    label {
      font-size: 0.7rem;
      font-weight: 600;
    }

    button {
      width: 100%;
    }
  }

  .form--register {
    label {
      font-size: 0.7rem;
      font-weight: 700;
    }
  }
}
