.top-nav {
  background-color: #333e48;
  padding: 10px 0;
  .top-nav__box {
    display: flex;
    justify-content: space-between;

    .brand {
      margin-bottom: 0;
      display: flex;
      .brand__name,
      h6 {
        margin-bottom: 0;
        align-self: center;
        color: $orange1;
      }
      h6 {
        margin-inline-start: 20px;
      }
    }
    .services {
      display: flex;
      align-self: center;
      a {
        margin: 0 7px;

        svg {
          width: 30px;
          height: 30px;
        }
        path {
          transition: all 0.3s ease-in-out;
        }
        &:hover path,
        &:hover circle {
          transition: all 0.3s ease-in-out;
          fill: $white;
        }
      }
      .lang {
        border: 2px solid $orange1;
        color: $orange1;
        border-radius: 5px;
        width: 30px;
        height: 30px;
        text-align: center;
        line-height: 1.5rem;
        font-size: 1rem;
        padding-inline-end: 2px;
        transition: all 0.3s ease-in-out;
        &:hover {
          transition: all 0.3s ease-in-out;
          color: $white;
          border-color: $white;
        }
      }
      h6 {
        margin: 0;
        padding-top: 7px;
        color: #fff;
        font-weight: bold;
        font-size: 12px;
      }

      p {
        color: #ddd;
        font-size: 10px;
        margin: 0;
      }
    }
  }
}
.butler-navbar {
  background-color: #333e48;
  border-top: 1px solid #515151;
  padding: 0;

  .navbar-nav .nav-item {
    border-inline-end: 1px solid #515151;
    padding: 5px 8px;
    .dropdown-menu {
      border: 1px solid #515151;
      border-top: 0;
      border-radius: 0;
      top: 40px;
      left: -1px;
      background-color: $blue2;
      margin-top: 0;

      .dropdown-item {
        color: #515151;
        transition: all 0.3s ease-in-out;
        font-size: 0.8rem;

        &:hover {
          color: $orange1;
          background-color: $blue2;
        }
        &:not(:last-of-type) {
          border-bottom: 1px solid $gray3;
        }
      }
    }
    .dropdown-submenu {
      position: relative;
    }

    .dropdown-submenu a::after {
      transform: rotate(-90deg);
      position: absolute;
      right: 6px;
      top: 0.8em;
    }

    .dropdown-submenu .dropdown-menu {
      top: 0;
      left: 100%;
      margin-left: 0.1rem;
      margin-right: 0.1rem;
    }
  }

  .navbar-nav .nav-item .nav-link {
    color: #707070;
    transition: color 0.3s ease-in-out;
    &:hover {
      transition: color 0.3s ease-in-out;
      color: $orange1;
    }
  }
  .navbar-nav .nav-item.active .nav-link {
    color: $orange1;
  }
  .search-form {
    position: relative;
  }
  .search-form .search-box {
    border-radius: 20px;
    height: 32px;
    width: 100%;
    &:focus {
      box-shadow: none;
      border-color: $orange1;
    }
  }
  .search-form__btn--search {
    position: absolute;
    right: 5px;

    &:focus {
      box-shadow: none;
    }
  }
}

