@media (max-width: 991px) {
  .top-nav {
    .top-nav__box {
      flex-direction: column-reverse;
      .brand {
        margin-bottom: 10px;
        position: relative;
        .brand__icon {
          img {
            width: 40px;
          }
        }
        .brand__name {
          font-size: 1.3rem;
        }
        h6 {
          font-size: 0.7rem;
          margin-inline-start: 20px;
        }
        .navbar-toggler {
          position: absolute;
          right: 5px;
          top: 50%;
          border-radius: 0;
          transform: translateY(-50%);
          border-color: $orange1;
          &:focus {
            outline: none;
          }
          .navbar-toggler-icon {
            display: block;
            height: 2px;
            background-color: $orange1;
            margin: 3px 0;
          }
        }
      }
      .services {
        margin-bottom: 10px;
        align-self: baseline;

        a {
          margin: 0 7px;

          svg {
            width: 25px;
            height: 25px;
          }
          path {
            transition: all 0.3s ease-in-out;
          }
          &:hover path,
          &:hover circle {
            transition: all 0.3s ease-in-out;
            fill: $white;
          }
        }
        .lang {
          width: 25px;
          height: 25px;
          text-align: center;
          line-height: 1.3rem;
          font-size: 0.6rem;
          margin-inline-start: 0;
        }
        h6 {
          padding-top: 2px;
        }

        p {
          color: #ddd;
          font-size: 10px;
          margin: 0;
        }
      }
    }
  }

  .butler-navbar {
    .navbar-nav .nav-item {
      border-bottom: 1px solid #515151;
      .dropdown-menu {
        border: none;
      }
    }
  }
}
