.product {
  span {
    color: $gray1;
  }
  .product__detial {
    color: $blue2;
  }
  .product__price {
    display: flex;
    margin-bottom: 5px;

    span {
      margin-inline-end: 10px;
    }
  }
  p {
    color: $gray1;
    line-height: 1.4rem;
  }
  .product__quantity {
    display: flex;

    span {
      margin-inline-end: 10px;
    }
    .form-control {
      border-radius: 20px;
      height: 25px;
      width: 60px;
      padding-right: 5px;
      &:focus {
        box-shadow: 0 0 0 0.2rem $orange1;
      }
    }
  }

  .product__btns {
    display: flex;
    justify-content: space-between;
    .btn {
      width: 47.5%;
    }
  }

  //   swiper thumb
  .swiper-container-related {
    width: 100%;
    height: 300px;
    margin-left: auto;
    margin-right: auto;
  }

  .swiper-slide {
    background-size: cover;
    background-position: center;
  }

  .gallery-top {
    height: 80%;
    width: 100%;
  }

  .gallery-thumbs {
    height: 20%;
    box-sizing: border-box;
    padding: 10px 0;
  }

  .gallery-thumbs .swiper-slide {
    height: 100%;
    opacity: 0.4;
  }

  .gallery-thumbs .swiper-slide-thumb-active {
    opacity: 1;
  }

  //   swiper thumb

  .related-heading {
    color: $gray1;
    margin: 30px 0 20px 0;
  }

  .gallery-top--product {
    position: relative;
    .zoom-btn {
      position: absolute;
      right: 10px;
      top: 10px;
      z-index: 9999;

      svg {
        fill: #fff;
        width: 20px;
      }
    }
  }
}

@media (max-width: 991px) {
  .product .product__detial {
    font-size: 1rem;
  }
  .product .gallery-top {
    min-width: 400px;
    margin-top: 30px;
  }
  .swiper-wrapper--product {
    height: 400px;
  }
}
