.page-content .section-heading {
  text-align: center;
  padding: 15px 0;
  color: #707070;
  font-weight: bold;
  font-size: 1.3rem;
}

.line-heading {
  position: relative;
  &::after {
    position: absolute;
    content: "";
    width: 40px;
    height: 2px;
    top: -2px;
    left: 1px;
    z-index: 3;
  }
}

.line-heading--orange::after {
  background-color: $orange1;
}
.line-heading--gray::after {
  background-color: $gray1;
}
