.account-d-a {
  .address__c {
    color: $gray1;
    .a-d {
      margin-bottom: 0;
      text-transform: capitalize;
    }
    p {
      font-size: 1rem;
    }
    .h-c {
      position: relative;
      a {
        position: absolute;
        right: 10px;
        top: 50%;
        font-size: 1rem;
        color: $orange1;
        &:hover{
          color: $orange2;
        }


      }
      h2 {
        color: $blue2;
        font-weight: bold;
        text-transform: capitalize;
      }
    }
    .btn-edit {
      text-transform: capitalize;
    }

   
  }
}

@media(max-width:991px){
  .account-d-a .btn-add{
    margin-top: 20px;
    width: 100%;
  }
  .account-d-a .address__c .h-c h2{
    font-size: 1.4rem;
  }

}