.account-details {
  form {
    label {
      color: $blue2;
      font-weight: 500;
      font-size: 0.8rem;
    }
    .form-control {
      border-radius: 20px;
    }
    select {
      background: url(data:image/svg+xml;base64,)
        no-repeat;
      background-position: right 5px top; /* Change the px value here to change the distance */
      -moz-appearance: none;
      -moz-appearance: none;
      -webkit-appearance: none;
      appearance: none;
      padding: 4px 20px;
    }
    .btn-delete {
      border-radius: 25px;
    }
  }
}
