.butler-main-slider {
  position: relative;

  .swiper-slide {
    img {
      width: 100%;
     height: 500px;
    }
    .slide-content {
      display: inline;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: 9999;
      text-align: center;
      h5 {
        background-color: #f0cc53;
        display: inline-block;
        color: #fff;
      }

      h3 {
        background-color: #ffff;
        font-weight: bold;
      }
      h5,
      h3 {
        padding: 5px 10px;
      }
    }
  }

  .swiper-button-prev:after,
  .swiper-button-next:after {
    transition: color 0.3s ease-in-out;
    color: #ebbb1f;
  }
  .swiper-button-prev:hover::after,
  .swiper-button-next:hover::after {
    transition: color 0.3s ease-in-out;
    color: #f0cc53;
  }

  .swiper-pagination-bullet-active {
    background-color: #f0cc53;
  }
}

@media (max-width: 992px) {
  .butler-main-slider {
    .swiper-slide {
      img {
        width: 100%;
        height: 200px;
      }
      .slide-content {
       
        h5 {
          font-size: 1rem;

        }

        h3 {
         font-size: 1.1rem;
         width: max-content;
        }
       
      }
    }

   
   

  
  }
}
