.successful-order {
  .success-icon {
    margin-top: 50px;
    svg {
      width: 200px;
      height: auto;
    }
  }
  h2 {
    color: $blue2;
  }
  p {
    color: $gray1;
    font-size: 1rem;
    a {
      color: $orange1;
      font-weight: bold;
      transition: color 0.3s ease-in-out;

      &:hover {
        color: $orange2;
        transition: color 0.3s ease-in-out;
      }
    }
  }

  .destination-group {
    margin: 45px 0;
    display: flex;
    justify-content: center;
    .btn {
      margin: 0 10px;
      width: 40%;
      border: 1px solid $orange1;
    }
    .btn--account {
      border: 1px solid $orange1;
      color: $orange1;
      background-color: $white;
      &:hover {
        border-color: $gray1;
        background-color: $gray1;
        color: $white;
      }
    }
  }
}
@media (max-width: 991px) {
  .successful-order .destination-group {
    flex-direction: column;
    .btn {
      width: 100%;
      margin: 10px 0;
    }
  }
}
