.cart {
  table {
    thead tr {
      vertical-align: middle;
      th {
        padding-top: 5px;
        padding-bottom: 5px;
        background-color: $gray3;
        color: #fff;
        border: 0;
        &:first-of-type {
          border-top-left-radius: 3px;
          border-bottom-left-radius: 3px;
        }
        &:last-of-type {
          border-top-right-radius: 3px;
          border-bottom-right-radius: 3px;
        }
      }
    }
    tbody tr {
      td {
        vertical-align: middle;
        color: $blue2;
        padding-top: 5px;
        padding-bottom: 5px;

        .btn-cancel {
          padding: 5px;
          background: none;
          border: none;
        }
        img {
          max-width: 50px;
        }
        .form-control--number {
          max-width: 70px;
          height: 30px;
          border-radius: 20px;
          padding: 5px 8px;
          text-align: center;
        }
      }
      &:first-of-type td {
        border-top: 0;
      }
    }
    tfoot {
      tr {
        td {
          border-top: none;
          padding-top: 10px;
          padding-bottom: 10px;
          background-color: $gray3;
          vertical-align: middle;
        }

        .coupon-c {
          display: flex;
          justify-content: space-between;
          align-content: center;
          div {
            align-self: center;
          }
          .apply-c {
            position: relative;
            .form-control {
              height: 34px;
              border-radius: 20px;
              &:focus {
                box-shadow: none;
                border: none;
              }
            }
            .btn-apply {
              position: absolute;
              top: 50%;
              transform: translateY(-50%);
              right: -20px;
            }
          }
        }
      }
      .t-b {
        font-weight: bold;
        font-size: 0.8rem;
      }
    }
  }

  .cart__d-t {
    .d-t__delivery,
    .d-t__total {
      padding: 10px 10px;
      display: flex;
      justify-content: space-between;
      color: $blue2;
      span {
        font-size: 0.8rem;
        font-weight: bold;
      }

      .total_num {
        font-weight: bold;
        font-size: 1.2rem;
      }
    }
    .d-t__total {
      border: 1px solid $orange1;
      margin-bottom: 10px;
    }
    .delivery__num {
      width: 50%;
      text-align: center;
    }
  }

 
}

@media (max-width: 991px) {
  .cart {
    .cart__form {
      display: block;
      overflow-x: auto;
      white-space: nowrap;
    }
  }
}
