* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
// @import url("https://fonts.googleapis.com/css?family=El+Messiri&display=swap");
@import url("../fonts/Segoe UI.ttf");
body {
  font-family: "Segoe UI", sans-serif, arial;
  background-color: #fff;
  font-size: 12px;
  border-top: 10px solid #f0cc53;
}
ul,
ol {
  list-style: none;
  padding-left: 0;
  padding-right: 0;
}
a {
  text-decoration: none;
  &:hover {
    text-decoration: none;
  }
}



