.btn {
  font-size: 0.9rem;
}
.btn-secondary {
  background-color: $blue1;
  border-color: $blue1;
}

.btn-primary {
  background-color: $orange1;
  color: $blue2;
  padding: 4px 15px;
  border-radius: 20px;
  border-color: $orange1;
  line-height: 1.7;
  font-size: 0.9rem;
  font-weight: bold;
  transition: all 0.3s ease-in-out;
  &:hover {
    transition: all 0.3s ease-in-out;
    background-color: $gray1;
    color: $white;
    border-color: $gray1;
  }
}

.btn-add {
  width: 45%;
  font-size: 0.8rem;
  padding-top: 0.6em;
  padding-bottom: 0.6em;
}

.btn--social {
  transition: all 0.3s ease-in-out;

  &:hover svg path {
    transition: all 0.3s ease-in-out;
    fill: $white;
  }
  svg {
    vertical-align: middle;
    margin-inline-end: 10px;
  }
}
.btn-social-apple {
  color: #000;
  border: 1px solid #000;
  background-color: transparent;
}

.btn-social-twitter {
  color: #03A9F4;
  border: 1px solid #03A9F4;
  background-color: transparent;
}
.btn-social-google {
  color: #de4d3b;
  border: 1px solid #de4d3b;
  background-color: transparent;
}
.btn-social-face {
  color: #4267b2;
  border: 1px solid #4267b2;
  background-color: transparent;
}
.btn-b-orange {
  border-color: $orange1;
  background-color: $white;
}
.btn-b-gray {
  background-color: $gray2;
  border-color: $gray2;
  color: $white;
}

@media (max-width: 991px) {
  .btn {
    font-size: 0.7rem;
  }
}
