footer {
  background-color: $blue2;
  padding-top: 20px;

  .f-top {
    padding-bottom: 30px;
    ul h6 {
      color: $orange1;
      font-weight: bold;
      margin-bottom: 15px;
    }
    ul li {
      padding: 3px 0;
      a {
        transition: all 0.3s ease-in-out;

        color: #fff;
        &:hover {
          transition: all 0.3s ease-in-out;
          color: $orange1;
          padding-inline-start: 2px;
        }
      }
    }
    .f-top__c {
      display: flex;
      .c__icon svg {
        max-width: 35px;
      }
      .c__box {
        h6,
        p {
          color: $gray3;
        }
        h6 {
          margin-top: 8px;
          margin-bottom: 7px;
          padding-inline-start: 7px;
          font-weight: bold;
        }
        p {
          font-size: 1rem;
        }
      }
    }
  }
  .f-bottom {
    border-top: 1px solid $gray1;
    padding-top: 5px;
    padding-bottom: 50px;
    background-color: $blue2;
    ul {
      display: flex;
    }
    .contact {
      h4 {
        color: #fff;
        font-weight: normal;
      }
      p {
        color: $gray2;
        font-size: 0.7rem;
        font-weight: bold;
      }
      ul li {
        margin-inline-end: 1rem;

        svg {
          transition: all 0.3s ease-in-out;
          fill: #7c7c7c;
        }
        &:hover svg {
          transition: all 0.3s ease-in-out;
          fill: $white;
        }
      }
    }

    .copyright {
      p {
        font-size: 1rem;
        color: $gray1;
      }
      ul {
        justify-content: space-between;
        li {
          svg {
            max-width: 7rem;
          }
          svg path {
            transition: all 0.3s ease-in-out;
          }
          &:hover svg path {
            fill: $orange1;
          }
        }
      }
    }
  }
}

@media (max-width: 991px) {
  footer {
    .f-top {
      padding-bottom: 0px;
      ul h6 {
        margin-bottom: 5px;
        font-size: 0.7rem;
      }
      ul li {
        padding: 3px 0;
        a {
          font-size: 10px;
        }
      }
      .f-top__c {
        .c__box {
          h6 {
            font-size: 0.7rem;
          }
          p {
            font-size: 0.7rem;
          }
        }
        .c__icon svg {
          max-width: 25px;
        }
      }
    }
    .f-bottom {
      .contact {
        h4 {
          font-size: 1rem;
        }
        p {
          font-size: 0.6rem;
        }
        ul li {
          margin-inline-end: 1rem;

          svg {
            width: 20px;
          }
        }
      }

      .copyright {
        p {
          font-size: 0.6rem;
        }
        ul {
          justify-content: space-between;
          li {
            svg {
              max-width: 3rem;
            }
          }
        }
      }
    }
  }
}
