.acc-history-o {
  .history-o__t {
    thead th {
      border-top: 1px solid $gray3;
      border-bottom: 1px solid $gray3;
    }
    tbody {
      tr {
        &:hover td {
          background-color: $gray3;
          transition: all 0.3s ease-in-out;
        }
      }
      tr td:last-of-type {
        text-align: end;
      }

      td {
        border: none;
        border-bottom: 1px solid $gray3;
        padding: 0.5em;
        vertical-align: middle;
      }
    }
  }
  .history-o__total-t {
    tr td {
      font-size: 1rem;
    }
    tr td:last-of-type {
      font-weight: bold;
      padding: 0.5em;
    }
  }
}

@media (max-width: 991px) {
  .acc-history-o {
    overflow-x: auto;
  }
}
