.p-box {
  margin-bottom: 15px;
}
.p-box--save {
  border: 1.5px solid $orange1;
  transition: all 0.3s ease-in-out;
  &:hover {
    transition: all 0.3s ease-in-out;
    box-shadow: 0px 2px 3px -1px $orange2;
  }
  a {
    text-decoration: none;
  }
  .p-box__c {
    display: flex;
    .p-box__content {
      padding: 20px 15px;
      width: 60%;
      .p-box__decs {
        font-size: 1rem;
      }
      .price {
        color: #b23232;
        font-size: 1.2rem;
      }
      .price-b-d {
        margin-inline-start: 2.5rem;
        color: #aaa;
        font-size: 0.7rem;
        text-decoration: line-through;
      }
      .sold,
      .avilable {
        color: $gray1;
        & span {
          font-weight: bold;
          margin-inline-end: 0.3rem;
        }
      }

      .progress {
        margin-top: 0.2rem;
        height: 0.5rem;
        .progress-bar {
          background-color: $blue1;
        }
      }
      .offer-ends {
        color: $gray1;
        margin-top: 0.5rem;
        margin-bottom: 0.3rem;
      }
      .o-e-date {
        color: $gray1;
        font-size: 1.3rem;
        margin-bottom: 0;
      }
    }
    .p-box__img {
      padding-top: 1px;
      width: 40%;
      img {
        width: 100%;
        height: 100%;
      }
      position: relative;
      .circle {
        position: absolute;
        background-color: $orange1;
        top: 15px;
        right: 15px;
        border-radius: 50%;
        padding: 10px;
        width: 60px;
        height: 60px;
        text-align: center;
        color: $white;
        .word {
          text-transform: uppercase;
          margin-top: -5px;
        }
        .saved {
          font-size: 1.7rem;
          font-weight: bold;
          margin-top: -5px;
        }
      }
    }
  }
}

.p-box--itop {
  box-shadow: 0px 0px 2px 0px rgb(85, 85, 85);
  transition: all 0.3s ease-in-out;
  &:hover {
    transition: all 0.3s ease-in-out;
    box-shadow: 0px 0px 4px 0px rgb(85, 85, 85);
  }
  .p-box__c {
    .p-box__img {
      img {
        max-height: 180px;
        width: 100%;
      }
    }
    .p-box__content {
      padding: 10px 15px;
      .p-box__type {
        font-size: 0.8rem;
        color: $gray2;
      }
      .p-box__decs {
        font-size: 0.8rem;
        color: $gray1;
        font-weight: bolder;
      }
      .price {
        color: $blue1;
        font-size: 1.2rem;
      }
      .price-b-d {
        margin-inline-start: 5rem;
        color: $gray1;
        font-size: 0.9rem;
        text-decoration: line-through;
      }
      .p-box__q-c {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .q-word {
          font-size: 1rem;
          color: $gray1;
          opacity: 0.8;
        }
        input {
          width: 65px;
          height: 30px;
          text-align: center;
          padding-inline-end: 2px;
          outline-color: transparent;
          color: $gray1;

          &:focus {
            outline-color: $blue1;
            transition: all 0.3s ease-in-out;
          }
        }
        button {
          display: inline-block;
          width: 50px;
          height: 35px;
          svg {
            vertical-align: baseline;
          }
        }
      }
    }
  }
}

.p-box--favorite {
  box-shadow: 0px 0px 2px 0px rgb(85, 85, 85);
  transition: all 0.3s ease-in-out;
  &:hover {
    transition: all 0.3s ease-in-out;
    box-shadow: 0px 0px 4px 0px rgb(85, 85, 85);
  }

  .p-box__c {
    position: relative;
    .favorite {
      position: absolute;
      right: 5px;
      top: 5px;
      svg {
        width: 20px;
        path {
          color: $gray2;
        }
      }
      .btn-cancel {
        border: none;
        background: none;
        font-size: 1rem;
      }
    }
    .p-box__img {
      img {
        max-height: 180px;
        width: 100%;
      }
    }
    .p-box__content {
      padding: 10px 5px;
      .p-box__type {
        font-size: 0.8rem;
        color: $gray2;
      }
      .p-box__decs {
        font-size: 0.8rem;
        color: $gray1;
        font-weight: bolder;
      }

      .price {
        color: $blue1;
        font-size: 1.2rem;
        display: block;
        text-align: center;
        padding-top: 5px;
      }
      .price-b-d {
        color: $gray1;
        font-size: 0.9rem;
        text-decoration: line-through;
        display: block;
        text-align: center;
      }
      .p-box__q-c {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .q-word {
          font-size: 1rem;
          color: $gray1;
          opacity: 0.8;
        }
        input {
          width: 50px;
          height: 30px;
          text-align: center;
          padding-inline-end: 2px;
          outline-color: transparent;
          color: $gray1;

          &:focus {
            outline-color: $blue1;
            transition: all 0.3s ease-in-out;
          }
        }
        button {
          display: inline-block;
          width: 35px;
          height: 35px;
          padding: 0;
          svg {
            vertical-align: baseline;
          }
        }
      }
    }
  }
}

@media (max-width: 991px) {
  .p-box--save .p-box__c .p-box__img .circle .saved {
    font-size: 1.3rem;
  }
  .p-box--favorite .p-box__c .p-box__content .p-box__q-c button {
    width: 40px;
  }
}
