.account-payment {
  .payment__card {
    padding: 0.8em 1.2em;
    border-radius: 0.5rem;
    background-color: $orange1;

    .card__a-n {
      display: flex;
      justify-content: space-between;

      .delete {
        align-self: center;
        svg {
          width: 18px;
          height: 18px;
          transition: all 0.3s ease-in-out;
        }
        &:hover svg {
          fill: #fff;
          transition: all 0.3s ease-in-out;
          cursor: pointer;
        }
      }
      .card-number {
        font-size: 1.7rem;
        color: $blue2;
      }
    }
    p {
      margin-bottom: 0;
      font-size: 10px;
      color: $gray1;
    }
    .card__serial {
      .serial__c {
        display: flex;
        justify-content: space-between;
        span {
          font-weight: 400;
          font-size: 1.4rem;
          color: #fff;
          line-height: 1;
        }
      }
    }

    .card__h-e {
      display: flex;
      justify-content: space-between;
      margin-top: 10px;

      span {
        font-size: 1rem;
        color: $blue2;
      }
      .h-e__exp{
          margin-inline-end:2em ;
      }
    }
  }
}

@media (max-width: 991px) {
}
