.contacts {
  li {
    label {
      color: $gray1;
      margin-bottom: 0;
    }
    p {
      color: $blue2;
      font-size: 1.3rem;
      margin-bottom: 0.3rem;
    }
  }
  .chat {
    svg {
      width: 30px;
      margin-inline-end: 10px;
    }
    span {
      align-self: center;
      font-size: 1.5rem;
    }
  }
  .cart {
    display: flex;
    align-items: center;
    svg {
      width: 20px;
      margin-inline-end: 10px;
    }
  }
  .so-b {
    color: $gray1;
  }
  .so-icon {
    margin-top: 10px;
    svg {
      margin-inline-end: 15px;
      width: 20px;

      path {
        fill: $gray1;
      }
    }
  }
}

@media (max-width: 991px) {
  .contacts {
    li p {
      font-size: 0.9rem;

      .chat {
        svg {
          width: 20px;
        }
        span {
          align-self: center;
          font-size: 1rem;
        }
      }
    }
  }
}
